@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
.navbar{
  padding: 0px !important;
  background-color: #383838 !important;
  margin-bottom: 20px;
}

.logo {
  width: 50px;
}

.nav-item {
  font-size: 20px;
  margin-left: 20px;
}

.nav-link.active{
  text-decoration: underline !important;
}
.footer {
    position: fixed !important;
    bottom: 0  !important;
    left: 50%;
    width: 94%;
    transform: translateX(-50%);
    height: 150px; /* Fixed height for footer*/
    line-height: 55px; /* Vertically center text accounting for 5px for footerline */
    text-align: center;
    background-color: #383838;
    padding-top: 10px;
}

.icon a {
  font-size: 40px;
  color: #6294C1;
  margin:10px;
}

.icon:hover a {
  font-size: 50px;
  color: #6294C1;
}

.inline {
  display: flex;
  justify-content: center;
}

.copyright {
  position: absolute;
  bottom: 10px;
  font-size: 16px;
  left: 50%;
  transform: translateX(-50%);
  color: #6294C1;
  font-family: sans-serif; 
}

 a img {
  width: 35px;
  height: 35px;
  margin-bottom: 11px;
}


a img:hover {
  width: 42px;
  height: 42px; 
} 
.profile-pic {
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.intro {
  font-size: 40px;
  color: #8AC5FC;
}
.intro span{
  color: #E7F3F9;
}

.home-bio {
  font-size: 30px;
  text-align: center;
  margin-bottom: 15px;
  color: #8AC5FC;
}

  

.profile-bio {
  font-size: 20px;
  text-align: justify;
  margin-bottom: 15px;
  color: #8AC5FC;
  
}

.profile-bio a {
  color: #E7F3F9 !important;
}
.maintitle h1 {
  font-size: 30px;
  color: #8AC5FC;
}

hr {
  border-top: 3px solid #31517D !important;
}

.card-img-overlay {
  opacity: 0;
  transition: .5s ease;
  background-color: #181A1B;
  text-align: center;
}

.card {
  max-width: 300px;
  background-color: #181A1B !important;
}

.btn-project{
  position: absolute;
  left: 50%;
  bottom: 20%;
  transform: translateX(-50%);
}
.card:hover .card-img-overlay{
  opacity: 1;
}

.card-img-overlay h3 {
  color: #8AC5FC;
}

.card-img-overlay p{
  color: #6294C1;
}

.card-img-overlay button{
  border-radius: 0 !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
}
.imageModal, .modal-body{
  padding: 0px !important;
}

.modal-body {
  background-color: #181A1B;
}

.modalText {
  padding: 10px !important;
  color: #8AC5FC;
}


.modalLink {
  text-align: center;
}

.modalLink a {
  width: 120px;
  border-radius: 0 !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
  margin: 2px;
}


.modalLink a:hover {
  background-color: #576b77 !important;
}

.modal-body button {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 30px;
  color: #032F70 !important;
}

.modal-body button:not(:hover) {
  opacity: 1;
  color: #032F70;
}


.projectbox {
  padding:0px !important;
  max-width: 300px !important;
}

.animated {
  animation-fill-mode: backwards !important;
}
.form-group {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}

.form-control {
  background-color: #839fb1 !important;
  font-size: 20px !important;
  border-radius: 0px !important;
}


.contact-text {
  text-align: justify;
  color: #8AC5FC;
  font-size: 20px;
  margin-bottom: 30px;
}

.contact-text span {
  color: #E7F3F9;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}

form {
  padding-left: 0px !important;
}

.div-button {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
}


.div-button button {
  font-size: 20px !important;
  background-color: #839fb1 !important;
  color: #E7F3F9  !important;
  border: none !important;
  border-radius: 0px !important;
}
.content {
  padding: 10px;
  font-family: 'Yusei Magic', sans-serif;
}
html{
  height:100%;
  width: 100%;
}
body { 
  min-height: 100%;  
  min-width: 100%;
}
body:after {
  content: "";
  position: fixed;
  background: #31517D;
  left: 0;
  right: 0;
  height: 10px;
}
body:after {
  bottom: 0;
  box-sizing: border-box;
}
body {
  
  border: 10px solid #31517D;
  background-color: #383838;
  padding-bottom: 160px;
  box-sizing: border-box;
}
